.loader {
  width: 100px;
  height: 100px;
  justify-content: space-between;
  margin: 20% auto;
  font-size: 0;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #ff3d00;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

